import axios from "axios";
import instance from "../components/Config/axios";
import {
  ERROR_ORDERS,
  ALL_ORDERS,
  Agency_Orders,
  ORDERS_BY_ID,
  ITEM_STATUS,
  ORDER_STATUS,
  ORDER_CANCEL,
} from "../constents/constents";

// here to get All AGENCY
export const allOrders = () => async (dispatch) => {
  try {
    const {
      data: { getAllOrders },
    } = await axios.get("/api/getAllOrders");
    // const { data: { getAllOrders } } = await axios.get(process.env.REACT_APP_API_URL + '/api/getAllOrders');

    dispatch({ type: ALL_ORDERS, payload: getAllOrders });
  } catch (error) {
    dispatch({ type: ERROR_ORDERS, payload: error.response.data.error });
  }
};

export const isreadOrder = (id) => async (dispatch) => {
  try {
    const {
      data: { isreadOrder },
    } = await axios.post(`/api/isreadorder`, { id });
    // const { data: { getAllOrders } } = await axios.get(process.env.REACT_APP_API_URL + '/api/getAllOrders');
    console.log("{isread}", isreadOrder);
    dispatch({ type: "isreadorder", payload: isreadOrder });
  } catch (error) {
    dispatch({ type: "isreadorder", payload: error.response.data.error });
  }
};
export const isOrderDelivered = (id) => async (dispatch) => {
  try {
    const {
      data: { isdelivered },
    } = await axios.post(`/api/isdelivered`, { id: id });

    console.log("{isdelivered}", isdelivered);
    dispatch({ type: "isorderdelivered", payload: isdelivered });
  } catch (error) {
    dispatch({ type: "isorderdelivered", payload: error.response.data.error });
  }
};
export const AgencyOrders = (id) => async (dispatch) => {
  try {
    const {
      data: { getAgencyOrders },
    } = await axios.get("/api/getAgencyOrders", {
      params: { id: id },
    });

    console.log("ActionsgetAgencyOrders :>> ", getAgencyOrders);
    dispatch({ type: Agency_Orders, payload: getAgencyOrders });
  } catch (error) {
    dispatch({ type: ERROR_ORDERS, payload: error.response.data.error });
  }
};

// Here to get Order by id
export const orderByID = (order_id) => {
  return async (dispatch) => {
    try {
      const {
        data: { getOrderDetails },
      } = await axios.get(`/api/getOrderDetails/${order_id}`);
      // const { data: { getOrderDetails } } = await axios.get(process.env.REACT_APP_API_URL + `/api/getOrderDetails/${order_id}`);

      dispatch({ type: ORDERS_BY_ID, payload: getOrderDetails });
    } catch (error) {
      dispatch({ type: ERROR_ORDERS, payload: error.response.data.error });
    }
  };
};

// Here to Change item status
export const orderItemStatusChange = (item_id, SelectedStatus) => {
  return async (dispatch) => {
    console.log(item_id, " ", SelectedStatus);
    try {
      const { data } = await axios.post(`/api/itemStatusUpdate`, {
        item_id,
        SelectedStatus,
      });
      // const { data } = await axios.post(process.env.REACT_APP_API_URL + `/api/itemStatusUpdate`, { item_id, SelectedStatus });

      dispatch({ type: ITEM_STATUS, payload: data });
    } catch (error) {
      dispatch({ type: ERROR_ORDERS, payload: error.response.data.error });
    }
  };
};

// Change order status

export const changeOrderStatus = (order_id) => {
  return async (dispatch) => {
    try {
      const { data } = await axios.get(
        `/api/updateOrderStatusAdmin/${order_id}`
      );
      // const { data } = await axios.get(process.env.REACT_APP_API_URL + `/api/updateOrderStatusAdmin/${order_id}`);

      dispatch({ type: ORDER_STATUS, payload: data });
    } catch (error) {
      dispatch({ type: ERROR_ORDERS, payload: error.response.data.error });
    }
  };
};

// Here Canceled order
export const cancelOrder = (order_id) => {
  return async (dispatch) => {
    try {
      const { data } = await axios.get(`/api/orderCancel/${order_id}`);
      // const { data } = await axios.get(process.env.REACT_APP_API_URL + `/api/orderCancel/${order_id}`);

      dispatch({ type: ORDER_CANCEL, payload: data });
    } catch (error) {
      dispatch({ type: ERROR_ORDERS, payload: error.response.data.error });
    }
  };
};
