import{ERROR_PRODUCT,ALL_PRODUCT,ADD_PRODUCT,DELETE_PRODUCT,EDIT_PRODUCT,ALL_CATEGORY,ADD_CATEGORY,CATEGORY_EDIT,DELETE_CATEGORY} from '../constents/constents';

const initState = {
    errors:[], 
};

const productReducer = (state = initState, action) => {
    switch(action.type){
     
            case ALL_CATEGORY:                      
            return{...state, getAllCategory: action.payload}   
            
            case ADD_CATEGORY:                      
            return{...state, categorySaved: action.payload,categoryEditUpdate:''} 

            case CATEGORY_EDIT:                      
            return{...state, categoryEditUpdate: action.payload,categorySaved:''}  
        
            case DELETE_CATEGORY:                      
            return{...state, deleteCategory: action.payload, errors:''} 

            case ALL_PRODUCT:                      
            return{...state, getAllProduct: action.payload, errors:'',deleteProduct:''}                
           
            case ADD_PRODUCT:                      
            return{...state, saveProduct: action.payload, errors:''}                
            
            case DELETE_PRODUCT:                      
            return{...state, deleteProduct: action.payload, errors:''}                
           
            case EDIT_PRODUCT:                      
            return{...state, productEdit: action.payload, errors:''}                
      
            case ERROR_PRODUCT:                    
                    return {...state, errors: action.payload};

        default:
        return state;
       
        }
 
}

export default productReducer;