import React, { useEffect, useState } from "react";
import { MDBDataTableV5 } from "mdbreact";
import { useDispatch, useSelector } from "react-redux";
import { Link, useParams } from "react-router-dom";
import toast, { Toaster } from "react-hot-toast";
import styles from "./style.module.css";
import {
  allbranches,
  allbranchesAgency,
  branchDeleteID,
} from "../../actions/agencyAction";

const BranchList = (props) => {
  const dispatch = useDispatch();
  const { branchesList, branchesListAgency, branchDelete } = useSelector(
    (state) => state.agencyReducer
  );
  const { user } = useSelector((state) => state.userReducer);
  const [tablebody, setTableBody] = useState([]);
  let count = 0;

  const { agencyId } = useParams();
  console.log("agencyId", agencyId);
  console.log("branchesListAgency", branchesListAgency);
  console.log("IDDD", user?._id);

  const [showconfirmation1, setshowconfirmation1] = useState(false);
  const [Id,setId]=useState("")

  const setvalues=(id)=>{
    setshowconfirmation1(true)
    setId(id)
  }

  useEffect(() => {
    if (user?.isAdmin) {
      dispatch(allbranches(agencyId));
    } else {
      console.log("Elseeeee");
      dispatch(allbranchesAgency(user?._id));
    }
  }, []);

  useEffect(() => {
    if (user?.isAdmin) {
      dispatch(allbranches(agencyId));
    } else {
      console.log("Elseeeee");
      dispatch(allbranchesAgency(user?._id));
    }
  }, []);

  // useEffect(() => {
  // 	dispatch(allbranches());
  // }, [])

  const DeleteBranch = () => {
    if(Id!==""){
    dispatch(branchDeleteID(Id));
  }
  setshowconfirmation1(false)
  };

  useEffect(() => {
    if (branchDelete) {
      toast.error(branchDelete.msg);
    }
    dispatch(allbranches());
  }, [branchDelete]);

  useEffect(() => {
    if (user?.isAdmin) {
      const userDataFiltered = branchesList?.map((x) => {
        console.log("aaaaa", x._id);
        let jsx = (
          <>
            <Link to={`branch-add/${x._id}`}>
              <button
                type="button"
                className="btn btn-sm btn-default"
                title="Edit Branch"
                data-for="send"
                data-tip="true"
                currentitem="false"
              >
                <i className="icon-pencil"></i>
              </button>
            </Link>
            <button
              type="button"
              onClick={() => setvalues(x._id)}
              className="btn btn-sm btn-default"
              title="Delete"
              data-for="send"
              data-tip="true"
              currentitem="false"
            >
              <i className="icon-trash"></i>
            </button>
            <Link
              to={`nurse-list-by-branch/${x._id}`}
              className="btn btn-sm btn-primary mr-1"
              title=""
            >
              See Nurses List
            </Link>
          </>
        );

        x.action = jsx;
        return x;
      });

      setTableBody({
        columns: [
          // {
          // 	label: 'Agency Name',
          // 	field: 'order_number',
          // 	width: 150,
          // },
          {
            label: "Branch Name",
            field: "branch_name",
            width: 150,
          },
          {
            label: "Action",
            field: "action",
            width: 10,
          },
        ],

        rows: userDataFiltered ? userDataFiltered : [],
      });
    } else {
      const userDataFiltered = branchesListAgency?.map((x) => {
        console.log("aaaaa", x);
        let jsx = (
          <>
            <Link to={`branch-add/${x._id}`}>
              <button
                type="button"
                className="btn btn-sm btn-default"
                title="Edit Branch"
                data-for="send"
                data-tip="true"
                currentitem="false"
              >
                <i className="icon-pencil"></i>
              </button>
            </Link>
            <button
              type="button"
              onClick={() => setvalues(x._id)}
              className="btn btn-sm btn-default"
              title="Delete"
              data-for="send"
              data-tip="true"
              currentitem="false"
            >
              <i className="icon-trash"></i>
            </button>
            <Link
              to={`branch-list-by-agency/nurse-list-by-branch/${x._id}`}
              className="btn btn-sm btn-primary mr-1"
              title=""
            >
              See Nurse List
            </Link>
          </>
        );

        x.action = jsx;
        return x;
      });

      setTableBody({
        columns: [
          // {
          // 	label: 'Agency Name',
          // 	field: 'order_number',
          // 	width: 150,
          // },
          {
            label: "Branch Name",
            field: "branch_name",
            width: 150,
          },
          {
            label: "Action",
            field: "action",
            width: 10,
          },
        ],

        rows: userDataFiltered ? userDataFiltered : [],
      });
    }
  }, [branchesList, branchesListAgency]);

  return (
    <>
      <div className="row clearfix">
        <div className="col-lg-12">
          <div className="card">
            <div className="header" style={{ marginTop: 20 }}>
              <h4>Branch List </h4>
              <Link
                to="/branch-add"
                className="btn btn-sm btn-primary mr-1"
                title=""
              >
                Add New Branch
              </Link>
              {/* <Link
                to="/nurse-add"
                className="btn btn-sm btn-primary mr-1"
                title=""
              >
                Add New Nurse
              </Link> */}

              <Toaster
                position="top-center"
                reverseOrder={false}
                toastOptions={{
                  style: {
                    border: "1px solid #713200",
                    padding: "16px",
                    color: "#713200",
                    fontSize: "17px",
                  },
                }}
              />
            </div>
            <div className="body">
              <div className="table-responsive">
                {branchesList?.length && (
                  <MDBDataTableV5
                    noBottomColumns
                    className="table table-hover js-basic-example dataTable table-custom spacing5"
                    entriesOptions={[5, 10, 20, 25]}
                    entries={5}
                    paging
                    pagesAmount={15}
                    data={tablebody}

                    // searchTop
                    // sortRows={['user_name']}
                    //searchBottom={true}
                    // onPageChange={()=>{ activePage: 2, pagesAmount: 5 }}
                  />
                )}
              </div>
            </div>
          </div>
          <div
            className={`${styles.modal} ${
              showconfirmation1 && `${styles.show}`
            }`}
            role="dialog"
            tabindex="-1"
            id="confirmation"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div
                  className={`${styles.modalheader}`}
                  style={{ backgroundColor: "#001c33", color: "white" }}
                >
                  <h4 className={`${styles.modaltitle}`}>Confirmation</h4>
                  <button
                  style={{color:"white"}}
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                    onClick={() => setshowconfirmation1(false)}
                  >
                    <span aria-hidden="true"></span>
                  </button>
                </div>
                <div className={`${styles.modalbody}`}>
                  <p style={{ color: "rgb(0,0,0)" }}>
                    Are you sure you want to delete this branch?
                    <br />
                  </p>
                </div>
                <div className={`${styles.modalfooter}`}>
                  <button
                    onClick={() => setshowconfirmation1(false)}
                    className="btn btn-light"
                    type="button"
                    data-dismiss="modal"
                    style={{
                      background: "rgb(242,242,242)",
                      fontFamily:
                        "&quot;Montserrat&quot;,&quot;Helvetica Neue&quot;,Helvetica,Arial,sans-serif",
                      fontWeight: "700",
                      color: "#000",
                      borderColor: "#f8f9fa",
                      display: "inline-block",
                      lineHeight: "1.5",
                      textAlign: "center",
                      textDecoration: "none",
                      verticalAlign: "middle",
                      cursor: "pointer",
                      border: "1px solid transparent",
                      padding: ".375rem .75rem",
                      fontSize: "1rem",
                      borderRadius: ".25rem",
                      transition:
                        "color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out",
                    }}
                  >
                    No
                  </button>
                  <button
                    onClick={(e) => DeleteBranch()}
                    data-dismiss="modal"
                    className="btn btn-primary"
                    type="button"
                    style={{
                      background: "#38ba7c",
                      color: "#fff",
                      fontFamily:
                        "&quot;Montserrat&quot;,&quot;Helvetica Neue&quot;,Helvetica,Arial,sans-serif",
                      fontWeight: "700",
                      display: "inline-block",
                      lineHeight: "1.5",
                      textAlign: "center",
                      textDecoration: "none",
                      verticalAlign: "middle",
                      border: "1px solid transparent",
                      padding: ".375rem .75rem",
                      fontSize: "1rem",
                      borderRadius: ".25rem",
                      transition:
                        "color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out",
                    }}
                  >
                    Yes
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
export default BranchList;
