import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { connect, useSelector, useDispatch } from 'react-redux';
import { toggleMegamenu, toggleSearchBar, toggleNotificationBar, setOffcanvas } from '../../actions/settingsAction';
import { signout } from '../../actions/userAction';
import { format } from 'timeago.js';


const Header = ({ toggleMegamenu, isMegaMenu, toggleNotificationBar, toggleSearchBar, setOffcanvas, offcanvas }) => {

    const { user } = useSelector((state) => state.userReducer);
    const { socket } = useSelector((state) => state.messangerReducer);

    const [notification, setNotification] = useState([]);
    const [status, setStatus] = useState([]);
    const [chatmsg, setChatMsg] = useState([]);

    // For New order
    useEffect(() => {
        socket.on("newOrder", (data) => {
            //console.log('mil gya 123',data);
            setNotification([...notification, data])
        });
        console.log('noti', notification);
    }, [notification]);

    // For order status
    useEffect(() => {
        socket.emit("get_messages", {
            "sender_id": user?._id,
        });

        socket.on("response", (data) => {
            if (data?.object_type == "get_message" && data?.data[0].sender_id !== user?._id) {
                setChatMsg([...chatmsg, data]) 
            }
        });
    }, [chatmsg]);

    // For order status
    useEffect(() => {
        socket.on("orderStatus", (data) => {
            //console.log('mil gya 123',data);
            setStatus([...status, data])
        });
        console.log('status', status);
    }, [status]);

    const dispatch = useDispatch();

    const signoutHendler = () => {
        dispatch(signout())
    }

    const [scrolled, setScrolled] = useState(0)
    const scrollProgress = () => {
        const scrollPx = document.documentElement.scrollTop;
        const winHeightPx =
            document.documentElement.scrollHeight -
            document.documentElement.clientHeight;
        const scrolleda = `${scrollPx / winHeightPx * 100}%`;
        setScrolled(scrolleda)
    };
    window.addEventListener("scroll", scrollProgress);

    const progressContainerStyle = {
        background: "#f8bbd0",
        boxShadow: "0 2px 4px rgba(0, 0, 0, 0.3)",
        height: "2px",
        position: "fixed",
        top: 0,
        left: 0,
        width: "100vw",
        zIndex: 99
    };

    const progressBarStyle = {
        // height: "2px",
        // background: "#e91e63",
        width: scrolled
    };
    //console.log(scrolled, 'nbnvhffhfgh');
    return (
        <>
            <nav className="navbar top-navbar">
                <div className="container-fluid">
                    <div className="navbar-left">
                        <div className="navbar-btn">
                            <Link to="/"><img src="../assets/images/icon.svg" alt="Oculux Logo" className="img-fluid logo" /></Link>
                            <button type="button" className="btn-toggle-offcanvas" onClick={() => setOffcanvas(!offcanvas)}><i className="lnr lnr-menu fa fa-bars"></i></button>
                        </div>
                        <ul className="nav navbar-nav">
                            <li className="dropdown">
                                <span className="dropdown-toggle icon-menu" data-toggle="dropdown">
                                    <i className="icon-envelope"></i>

                                    {status.length > 0 ? <span className="notification-dot bg-green">{status.length}</span> : ''}
                                </span>
                                <ul className="dropdown-menu right_chat email vivify fadeIn">
                                    {status.length > 0 ?
                                        <>
                                            <li className="header green">You Have {status.length} Order Status Change</li>
                                            {status.map((sts) =>

                                                <li key={sts}>
                                                    <Link to={`/order-details/${sts}`}>
                                                        <div className="media">

                                                            <div className="media-body">
                                                                <span className="name">View Order Now</span>
                                                            </div>
                                                        </div>
                                                    </Link>
                                                </li>
                                            )}
                                        </>
                                        : ''}
                                </ul>
                            </li>
                            <li className="dropdown">
                                <span className="dropdown-toggle icon-menu" data-toggle="dropdown">
                                    <i className="icon-bell"></i>
                                    {notification.length > 0 ? <span className="notification-dot bg-azura">{notification.length}</span> : ''}
                                </span>
                                <ul className="dropdown-menu feeds_widget vivify fadeIn">
                                    {notification.length > 0 ?
                                        <> <li className="header blue">You have {notification.length} New Order </li>
                                            {notification.map((res) =>
                                                <li>
                                                    <a href="/order-list">
                                                        <div className="feeds-left bg-red"><i className="fa fa-check"></i></div>
                                                        <div className="feeds-body">
                                                            <h4 className="title text-danger">New Order Number {res.order_number}<small className="float-right text-muted">{format(res.createdAt)}</small></h4>

                                                        </div>
                                                    </a>
                                                </li>
                                            )}
                                        </>
                                        : ''}

                                </ul>
                            </li>

                            <li className="dropdown">
                                <span className="dropdown-toggle icon-menu" data-toggle="dropdown">
                                    <i className="icon-bubbles"></i>
                                    {chatmsg.length > 0 ? <span className="notification-dot bg-azura">{chatmsg.length}</span> : ''}
                                </span>
                                <ul className="dropdown-menu feeds_widget vivify fadeIn">
                                    {chatmsg.length > 0 ?
                                        <> <li className="header blue">You have {chatmsg.length} New Message </li>
                                            {chatmsg.map((msg) =>(
                                                 <li>
                                                    <Link to="/app-chat"><div className="feeds-left bg-red"><i className="fa fa-check"></i></div>
                                                        <div className="feeds-body">
                                                            <h4 className="title text-danger">New Message: {msg?.data[0]?.message}<small className="float-right text-muted">{format(msg.createdAt)}</small></h4>

                                                        </div></Link> 
                                                </li>
                                            )
                                            )}
                                        </>
                                        : ''}

                                </ul>
                            </li>

                        </ul>
                    </div>

                    <div className="navbar-right">
                        <div id="navbar-menu">
                            <ul className="nav navbar-nav">
                                {/* <li><span onClick={() => toggleSearchBar(true)} className="search_toggle icon-menu" title="Search Result"><i className="icon-magnifier"></i></span></li>
                                <li><span onClick={() => toggleNotificationBar(true)} className="right_toggle icon-menu" title="Right Menu"><i className="icon-bubbles"></i><span className="notification-dot bg-pink">2</span></span></li> */}
                                <li><Link to="/login" onClick={signoutHendler} className="icon-menu"><i className="icon-power"></i></Link></li>
                            </ul>
                        </div>
                    </div>
                </div>
                <div className="progress-container">
                    <div style={progressBarStyle} className="progress-bar" id="myBar"></div></div>
            </nav>
        </>
    );
}

const mapStateToProps = state => ({
    isMegaMenu: state.settings.isMegaMenu,
    offcanvas: state.settings.offcanvas,
})

const mapDispatchToProps = dispatch => ({
    toggleMegamenu: (e) => dispatch(toggleMegamenu(e)),
    toggleSearchBar: (e) => dispatch(toggleSearchBar(e)),
    toggleNotificationBar: (e) => dispatch(toggleNotificationBar(e)),
    setOffcanvas: (e) => dispatch(setOffcanvas(e)),
})
export default connect(mapStateToProps, mapDispatchToProps)(Header);