import React,{useEffect, useState} from 'react';
import {useDispatch,useSelector} from 'react-redux';
import { dashbaordData } from '../../actions/userAction';
import axios from 'axios';


const Dashboard = () => {

	
	const dispatch = useDispatch();
	const {getDashbaord} = useSelector((state) => state.userReducer);
	const {user} = useSelector((state) => state.userReducer);
	const [agencyDetails,setagencyDetails]=useState([])
	const id= user?._id

	console.log("id",id)
	
	const getagencydashboard=()=>{
		
			axios.get(`https://server.appsstaging.com:3009/api/agencydashboard`, {
				params: {
				  id: id
				},
			}).then((res) => {
				console.log("res.data",res.data)
				setagencyDetails(res.data);
			});
			
		 
	}
	 console.log("usseerrr",user)
	 
	 
	 useEffect(() => {
           dispatch(dashbaordData());
		   getagencydashboard()
	 },[])

console.log("AD",agencyDetails)
	return (
		<div className="container-fluid">
			<div className="block-header">
				<div className="row clearfix">
					<div className="col-md-6 col-sm-12">
						<h1>{user?.isAdmin ?"Welcome To Redrock Admin":"Welcome To Redrock Agency"}</h1>						 
					</div>					
				</div>
			</div>

			<div className="row clearfix">
				<div className="col-lg-3 col-md-6">
					<div className="card">
						<div className="body">
							<div className="d-flex align-items-center">
								<div className="icon-in-bg bg-indigo text-white rounded-circle"><i className="fa fa-users"></i></div>
								<div className="ml-4">
									<span>{user?.isAdmin ? "Total Nurse" : "Total Branches"}</span>
									<h4 className="mb-0 font-weight-medium">{user?.isAdmin ? getDashbaord?.nurse : agencyDetails?.branches}</h4>
								</div>
							</div>
						</div>
					</div>
				</div>
				<div className="col-lg-3 col-md-6">
					<div className="card">
						<div className="body">
							<div className="d-flex align-items-center">
								<div className="icon-in-bg bg-azura text-white rounded-circle"><i className="fa fa-users"></i></div>
								<div className="ml-4">
									<span>{user?.isAdmin?"Total Agencies":"Total Nurses"}</span>
									<h4 className="mb-0 font-weight-medium">{user?.isAdmin ? getDashbaord?.agencies: agencyDetails?.nurse}</h4>
								</div>
							</div>
						</div>
					</div>
				</div>
				{user.isAdmin?(<div className="col-lg-3 col-md-6">
					<div className="card">
						<div className="body">
							<div className="d-flex align-items-center">
								<div className="icon-in-bg bg-orange text-white rounded-circle"><i className="fa fa-briefcase"></i></div>
								<div className="ml-4">
									<span>Android Users</span>
									<h4 className="mb-0 font-weight-medium">{getDashbaord?.android}</h4>
								</div>
							</div>
						</div>
					</div>
				</div>):null}
				
				{user.isAdmin?(<div className="col-lg-3 col-md-6">
					<div className="card">
						<div className="body">
							<div className="d-flex align-items-center">
								<div className="icon-in-bg bg-orange text-white rounded-circle"><i className="fa fa-briefcase"></i></div>
								<div className="ml-4">
									<span>IOS Users</span>
									<h4 className="mb-0 font-weight-medium">{getDashbaord?.ios}</h4>
								</div>
							</div>
						</div>
					</div>
				</div>):null}
				
			</div>

			
		</div>
	);

}
export default Dashboard
