import React, { useEffect, useState } from "react";
import { useParams, useHistory, Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import toast, { Toaster } from "react-hot-toast";
import Tooltip from "../common/toolTip";
import {
  allOrders,
  cancelOrder,
  changeOrderStatus,
  orderByID,
  orderItemStatusChange,
  isreadOrder,
  isOrderDelivered,
} from "../../actions/orderAction";

const OrderDetails = (props) => {
  const dispatch = useDispatch();
  const { getOrderDetails, itemStatus } = useSelector(
    (state) => state.orderReducer
  );
  const [orderItemStatusSelected, setOrderItemStatusSelected] = useState([]);
  const { orderList, orderStaus, orderCancel, isdelivered } = useSelector(
    (state) => state.orderReducer
  );
  const { user } = useSelector((state) => state.userReducer);

  const { order_id } = useParams();
  console.log("order_id", order_id);
  console.log("userrrrr", user);

  const [state, setState] = useState({
    id: "",
    agencyName: "",
    agencyDescription: "",
    email: "",
  });

  const handleInputs = (e) => {
    setState({
      ...state,
      [e.target.name]: e.target.value,
    });
  };

  // for Get Order By ID
  useEffect(() => {
    dispatch(orderByID(order_id));
    dispatch(isreadOrder(order_id));
    dispatch(isOrderDelivered(order_id));
  }, [order_id]);

  // useEffect(() => {
  //   const arr = [];
  //   getOrderDetails?.orderItems.map((od) => {
  //     arr.push(od);
  //   });
  //   console.log("arr :>> ", arr);
  // }, []);

  console.log("isdelivered :>> ", isdelivered);

  useEffect(() => {
    if (itemStatus) {
      toast.success(itemStatus.msg);
    }
    dispatch(orderByID(order_id));
  }, [itemStatus]);

  useEffect(() => {
    if (orderStaus) {
      toast.success(orderStaus.msg);
      dispatch(orderByID(order_id));
    }
  }, [orderStaus]);

  useEffect(() => {
    if (orderCancel) {
      toast.success(orderCancel.msg);
      dispatch(orderByID(order_id));
    }
  }, [orderCancel]);

  const changeStatus = (e, item_id, SelectedStatus) => {
    e.preventDefault();
    dispatch(orderItemStatusChange(item_id, SelectedStatus));
    dispatch(isOrderDelivered(order_id));
  };

  const handleOrderItemStatusChange = (e, index) => {
    let newArr = [...orderItemStatusSelected];
    newArr[index] = e.target.value;
    setOrderItemStatusSelected(newArr);
  };

  const changestatus = (e) => {
    e.preventDefault();
    dispatch(changeOrderStatus(order_id));
    dispatch(isOrderDelivered(order_id));
  };

  const deleteOrder = (e) => {
    e.preventDefault();
    dispatch(cancelOrder(order_id));
  };

  return (
    <>
      <div className="container-fluid">
        <div className="block-header">
          <div className="row clearfix">
            <div className="col-md-6 col-sm-12">
              <h1>Order Details</h1>
            </div>
          </div>
        </div>

        <Toaster
          position="top-center"
          reverseOrder={false}
          toastOptions={{
            style: {
              border: "1px solid #713200",
              padding: "16px",
              color: "#713200",
              fontSize: "17px",
            },
          }}
        />
       <div className="row clearfix">
          <div className="col-lg-4 col-md-12">
            <div className="card">
              <div className="header">{/* <h2>Users</h2> */}</div>
              <div className="body">
                <div className="row text-center">
                  <div className="col-6 border-right pb-4 pt-4">
                    <label className="mb-0">Order Number</label>
                    <h4 className="font-30 font-weight-bold text-col-blue">
                      {getOrderDetails?.order_number}
                    </h4>
                  </div>
                  <div className="col-6 pb-4 pt-4">
                    <label className="mb-0">Order Status</label>
                    <h4 className="font-30 font-weight-bold text-col-blue">
                      {getOrderDetails?.order_status}
                    </h4>
                  </div>
                </div>
              </div>
              <div className="body">
                <div className="form-group">
                  <label className="d-block">
                    Nurse Name{" "}
                    <span className="float-right">
                      {getOrderDetails?.nurse_id?.user_fname}{" "}
                      {getOrderDetails?.nurse_id?.user_lname}
                    </span>
                  </label>
                  <div className="progress progress-xxs">
                    <div
                      className="progress-bar progress-bar-warning"
                      role="progressbar"
                      aria-valuenow="50"
                      aria-valuemin="0"
                      aria-valuemax="100"
                      style={{ width: "50%" }}
                    ></div>
                  </div>
                </div>
                <div className="form-group">
                  <label className="d-block">
                    Patient Name{" "}
                    <span className="float-right">
                      {getOrderDetails?.patient_id.patient_fname}{" "}
                      {getOrderDetails?.patient_id?.patient_lname}
                    </span>
                  </label>
                  <div className="progress progress-xxs">
                    <div
                      className="progress-bar progress-bar-success"
                      role="progressbar"
                      aria-valuenow="77"
                      aria-valuemin="0"
                      aria-valuemax="100"
                      style={{ width: "77%" }}
                    ></div>
                  </div>
                </div>
                <div className="form-group">
                  <label className="d-block">
                    Patient Gender{" "}
                    <span className="float-right">
                      {getOrderDetails?.patient_id.patient_gender}
                    </span>
                  </label>
                  <div className="progress progress-xxs">
                    <div
                      className="progress-bar progress-bar-success"
                      role="progressbar"
                      aria-valuenow="77"
                      aria-valuemin="0"
                      aria-valuemax="100"
                      style={{ width: "77%" }}
                    ></div>
                  </div>
                </div>
                
                <div className="form-group">
                  <label className="d-block">
                    Patient Address{" "}
                   
                    <br />
                    
                      {getOrderDetails?.patient_id?.patient_address}
                      <br />
                    
                  </label>
                  <div className="progress progress-xxs">
                    <div
                      className="progress-bar progress-bar-success"
                      role="progressbar"
                      aria-valuenow="77"
                      aria-valuemin="0"
                      aria-valuemax="100"
                      style={{ width: "77%" }}
                    ></div>
                  </div>
                </div>
                
                <div className="form-group">
                  <label className="d-block">
                    Patient Address2{" "}
                    
                    <br />
                    
                    {getOrderDetails?.patient_id?.patient_address2}
                    <br />
                  </label>
                  <div className="progress progress-xxs">
                    <div
                      className="progress-bar progress-bar-success"
                      role="progressbar"
                      aria-valuenow="77"
                      aria-valuemin="0"
                      aria-valuemax="100"
                      style={{ width: "77%" }}
                    ></div>
                  </div>
                </div>
                <div className="form-group">
                  <label className="d-block">
                    Patient Contact{" "}
                    <span className="float-right">
                      {getOrderDetails?.patient_id?.patient_phone}
                    </span>
                  </label>
                  <div className="progress progress-xxs">
                    <div
                      className="progress-bar progress-bar-success"
                      role="progressbar"
                      aria-valuenow="77"
                      aria-valuemin="0"
                      aria-valuemax="100"
                      style={{ width: "77%" }}
                    ></div>
                  </div>
                </div>
                <div className="form-group">
                  <label className="d-block">
                    Patient City{" "}
                    <span className="float-right">
                      {getOrderDetails?.patient_id?.patient_city}
                    </span>
                  </label>
                  <div className="progress progress-xxs">
                    <div
                      className="progress-bar progress-bar-success"
                      role="progressbar"
                      aria-valuenow="77"
                      aria-valuemin="0"
                      aria-valuemax="100"
                      style={{ width: "77%" }}
                    ></div>
                  </div>
                </div>
                <div className="form-group">
                  <label className="d-block">
                    Patient Country{" "}
                    <span className="float-right">
                      {getOrderDetails?.patient_id?.patient_country}
                    </span>
                  </label>
                  <div className="progress progress-xxs">
                    <div
                      className="progress-bar progress-bar-success"
                      role="progressbar"
                      aria-valuenow="77"
                      aria-valuemin="0"
                      aria-valuemax="100"
                      style={{ width: "77%" }}
                    ></div>
                  </div>
                </div>
                <div className="form-group">
                  <label className="d-block">
                    Agency Name{" "}
                    <span className="float-right">
                      {getOrderDetails?.nurse_id.agency_id.hospital_name}
                    </span>
                  </label>
                  <div className="progress progress-xxs">
                    <div
                      className="progress-bar progress-bar-info"
                      role="progressbar"
                      aria-valuenow="23"
                      aria-valuemin="0"
                      aria-valuemax="100"
                      style={{ width: "23%" }}
                    ></div>
                  </div>
                </div>
                <div className="form-group">
                  <label className="d-block">
                    Branch Name{" "}
                    <span className="float-right">
                      {getOrderDetails?.nurse_id.branch_id?.branch_name}
                    </span>
                  </label>
                  <div className="progress progress-xxs">
                    <div
                      className="progress-bar progress-bar-info"
                      role="progressbar"
                      aria-valuenow="23"
                      aria-valuemin="0"
                      aria-valuemax="100"
                      style={{ width: "23%" }}
                    ></div>
                  </div>
                </div>

                <div className="form-group">
                  <label className="d-block">
                    Delivery Date{" "}
                    <span className="float-right">
                      {new Date(
                        getOrderDetails?.order_delivery_date
                      ).toLocaleDateString()}
                    </span>
                  </label>
                  <div className="progress progress-xxs">
                    <div
                      className="progress-bar progress-bar-info"
                      role="progressbar"
                      aria-valuenow="23"
                      aria-valuemin="0"
                      aria-valuemax="100"
                      style={{ width: "23%" }}
                    ></div>
                  </div>
                </div>
                <div className="form-group">
                  <label className="d-block">
                    Delivery Time{" "}
                    <span className="float-right">
                     
                        {getOrderDetails?.order_delivery_date.substring(11,19)}
                    </span>
                  </label>
                  <div className="progress progress-xxs">
                    <div
                      className="progress-bar progress-bar-info"
                      role="progressbar"
                      aria-valuenow="23"
                      aria-valuemin="0"
                      aria-valuemax="100"
                      style={{ width: "23%" }}
                    ></div>
                  </div>
                </div>

                <div className="body">
                  <div className="row text-center">
                    <div className="col-6 border-right pb-4 pt-4">
                      {/* <label className="mb-0">Order Title</label> */}
                      <h6 className="mb-0">Message</h6>
                      {/* <h4 className="font-30 font-weight-bold text-col-blue">{getOrderDetails?.order_title}</h4> */}
                      <p>{getOrderDetails?.order_title}</p>
                    </div>
                    <div className="col-6 pb-4 pt-4">
                      {/* <label className="mb-0">Order Details</label> */}
                      <h6 className="mb-0">Delivery Notes</h6>
                      {/* <h4 className="font-30 font-weight-bold text-col-blue">{getOrderDetails?.order_details}</h4> */}
                      <p>{getOrderDetails?.order_details}</p>
                    </div>
                  </div>
                </div>
                <div style={{ margin: "12px", marginLeft: "45px" }}>
                  {user?.isAdmin &&
                  getOrderDetails?.order_status !== "delivered" ? (
                    <button
                      className="btn btn-primary btn-round"
                      onClick={(e) => changestatus(e)}
                    >
                      {getOrderDetails?.order_status === "close"
                        ? "Re Open Order"
                        : "Close Order"}
                    </button>
                  ) : null}
                  {user?.isAdmin &&
                  getOrderDetails?.order_status !== "delivered" ? (
                    <button
                      className="btn btn-primary btn-round"
                      onClick={(e) => deleteOrder(e)}
                    >
                      {getOrderDetails?.is_blocked === 1
                        ? "Uncancel Order"
                        : "Cancel Order"}
                    </button>
                  ) : null}
                </div>
              </div>
            </div>
          </div>

          <div className="col-lg-8 col-md-12">
            <h4>Order Items</h4>
            <div className="table-responsive">
              <table className="table table-hover table-custom spacing5">
                <thead>
                  <tr>
                    <th style={{ width: "50px" }}>Product Name</th>
                    <th style={{ width: "50px" }}>Quantity</th>
                    <th style={{ width: "50px" }}>Product Status</th>
                    {user?.isAdmin ? (
                      <th style={{ width: "110px" }}>Status</th>
                    ) : null}
                    {user?.isAdmin ? (
                      <th style={{ width: "110px" }}>Action</th>
                    ) : null}
                  </tr>
                </thead>
                <tbody>
                  {getOrderDetails?.orderItems.map((ite, index) => (
                    <tr key={ite._id}>
                      <td>{ite.product_name}</td>
                      <td>{ite.quantity}</td>
                      <td>
                        <span className="badge badge-success ml-0 mr-0">
                          {ite.product_status}
                        </span>
                      </td>
                      {user?.isAdmin ? (
                        <td>
                          <div className="col-lg-12 col-md-12">
                            <div className="form-group">
                              <select
                                className="form-control"
                                value={orderItemStatusSelected[index]}
                                onChange={(e) =>
                                  handleOrderItemStatusChange(e, index)
                                }
                              >
                                <option value="">-- Select Status --</option>
                                <option value="ordered">ordered</option>
                                <option value="processed">processed</option>
                                <option value="out-for-delivery">
                                  out-for-delivery
                                </option>
                                <option value="delivered">delivered</option>
                                <option value="pickup-requested">
                                  pickup-requested
                                </option>
                                <option value="picked-up">picked-up</option>
                              </select>
                            </div>
                          </div>
                        </td>
                      ) : null}
                      {user?.isAdmin ? (
                        <td>
                          <button
                            className="btn btn-primary btn-round"
                            onClick={(e) =>
                              changeStatus(
                                e,
                                ite._id,
                                orderItemStatusSelected[index]
                              )
                            }
                            disabled={
                              getOrderDetails?.order_status == "close"
                                ? true
                                : false
                            }
                          >
                            Change
                          </button>
                        </td>
                      ) : null}
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
export default OrderDetails;
