import React, { useEffect, useState } from "react";
import { MDBDataTableV5 } from "mdbreact";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import toast, { Toaster } from "react-hot-toast";
import { agencyDeleteID, allAgency } from "../../actions/agencyAction";
import Button from "@material-ui/core/Button";
import styles from "./style.module.css";

const AgencyList = (props) => {
  const dispatch = useDispatch();
  const { agencyList, agencyDelete } = useSelector(
    (state) => state.agencyReducer
  );
  const { user } = useSelector((state) => state.userReducer);
  const [showconfirmation1, setshowconfirmation1] = useState(false);
  const [Id,setId]=useState("")

  console.log("user?._id", user?._id);

  const [tablebody, setTableBody] = useState([]);
  let count = 0;

  console.log("agencyList_in_component_file:", agencyList);

  const setvalues=(id)=>{
    setshowconfirmation1(true)
    setId(id)
  }

  useEffect(() => {
    dispatch(allAgency());
  }, []);

  const DeleteAgency = () => {
    if(Id!==""){
    dispatch(agencyDeleteID(Id));
    }
    setshowconfirmation1(false)
  };

  useEffect(() => {
    if (agencyDelete) {
      toast.error(agencyDelete.msg);
    }
    dispatch(allAgency());
  }, [agencyDelete]);

  console.log('showconfirmation1', showconfirmation1)

  useEffect(() => {
    if (agencyList) {
      const userDataFiltered = agencyList?.map((x) => {
        let jsx = (
          <>
            <Link to={`agency-add/${x._id}`}>
              <button
                type="button"
                className="btn btn-sm btn-default"
                title="Agency Details"
                data-for="send"
                data-tip="true"
                currentitem="false"
              >
                <i class="fa fa-eye" aria-hidden="true"></i>
              </button>
            </Link>
            <button
              type="button"
              onClick={() => setvalues(x._id)}
              className="btn btn-sm btn-default"
              title="Delete"
              data-for="send"
              data-tip="true"
              currentitem="false"
              data-target="#confirmation"
              data-toggle="modal"
            >
              <i className="icon-trash"></i>
            </button>
            <Link
              to={`branch-list-by-agency/${x._id}`}
              className="btn btn-sm btn-primary mr-1"
              title=""
            >
              See branch
            </Link>

   
          </>
          
        );
        x.action = jsx;
        return x;
      });

      setTableBody({
        columns: [
          // {
          // 	label: 'Agency Name',
          // 	field: 'order_number',
          // 	width: 150,
          // },
          {
            label: "Agency Name",
            field: "hospital_name",
            width: 150,
          },
          {
            label: "Email",
            field: "email",
            width: 150,
          },
          {
            label: "Action",
            field: "action",
            width: 10,
          },
        ],

        rows: userDataFiltered ? userDataFiltered : [],
      });
    }
  }, [agencyList]);

  return (
    <>
      <div className="row clearfix">
        <div className="col-lg-12">
          <div className="card">
            <div className="header" style={{ marginTop: 20 }}>
              <h4>{user?.isAdmin ? "Agency List" : "Nurses List"}</h4>
              <Link
                to="/agency-add"
                className="btn btn-sm btn-primary mr-1"
                title=""
              >
                {user?.isAdmin ? "Add new Agency" : "Add new Nurse"}
              </Link>

              <Toaster
                position="top-center"
                reverseOrder={false}
                toastOptions={{
                  style: {
                    border: "1px solid #713200",
                    padding: "16px",
                    color: "#713200",
                    fontSize: "17px",
                  },
                }}
              />
            </div>
            <div className="body">
              <div className="table-responsive">
                {agencyList?.length && (
                  <MDBDataTableV5
                    noBottomColumns
                    className="table table-hover js-basic-example dataTable table-custom spacing5"
                    entriesOptions={[5, 10, 20, 25]}
                    entries={5}
                    paging
                    pagesAmount={15}
                    data={tablebody}
                    // sortRows={['user_name']}
                  />
                )}
              </div>

              {/* {


							// filterUsers?.data.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((items, index) => (
							// allUsers?.data.map((items, index) => (
							agencyList?.map((items) => (
								// <TableSingleRow items={items} index={index} purchaseUsers={purchaseUsers} length={filterUsers?.data.length} />
								// <TableSingleRow items={items} index={index} purchaseUsers={purchaseUsers} length={filterUsers?.data.length} />
								<h3>{items.hospital_name}</h3>
							))


						} */}

            </div>
            <div
            className={`${styles.modal} ${
              showconfirmation1 && `${styles.show}`
            }`}
            role="dialog"
            tabindex="-1"
            id="confirmation"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div
                  className={`${styles.modalheader}`}
                  style={{ backgroundColor: "#001c33", color: "white" }}
                >
                  <h4 className={`${styles.modaltitle}`}>Confirmation</h4>
                  <button
                  style={{color:"white"}}
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                    onClick={() => setshowconfirmation1(false)}
                  >
                    <span aria-hidden="true"></span>
                  </button>
                </div>
                <div className={`${styles.modalbody}`}>
                  <p style={{ color: "rgb(0,0,0)" }}>
                    Are you sure you want to delete this agency?
                    <br />
                  </p>
                </div>
                <div className={`${styles.modalfooter}`}>
                  <button
                    onClick={() => setshowconfirmation1(false)}
                    className="btn btn-light"
                    type="button"
                    data-dismiss="modal"
                    style={{
                      background: "rgb(242,242,242)",
                      fontFamily:
                        "&quot;Montserrat&quot;,&quot;Helvetica Neue&quot;,Helvetica,Arial,sans-serif",
                      fontWeight: "700",
                      color: "#000",
                      borderColor: "#f8f9fa",
                      display: "inline-block",
                      lineHeight: "1.5",
                      textAlign: "center",
                      textDecoration: "none",
                      verticalAlign: "middle",
                      cursor: "pointer",
                      border: "1px solid transparent",
                      padding: ".375rem .75rem",
                      fontSize: "1rem",
                      borderRadius: ".25rem",
                      transition:
                        "color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out",
                    }}
                  >
                    No
                  </button>
                  <button
                    onClick={(e) => DeleteAgency()}
                    data-dismiss="modal"
                    className="btn btn-primary"
                    type="button"
                    style={{
                      background: "#38ba7c",
                      color: "#fff",
                      fontFamily:
                        "&quot;Montserrat&quot;,&quot;Helvetica Neue&quot;,Helvetica,Arial,sans-serif",
                      fontWeight: "700",
                      display: "inline-block",
                      lineHeight: "1.5",
                      textAlign: "center",
                      textDecoration: "none",
                      verticalAlign: "middle",
                      border: "1px solid transparent",
                      padding: ".375rem .75rem",
                      fontSize: "1rem",
                      borderRadius: ".25rem",
                      transition:
                        "color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out",
                    }}
                  >
                    Yes
                  </button>
                </div>
              </div>
            </div>
          </div>
          </div>
        </div>
      </div>
    </>
  );
};
export default AgencyList;
