import {
  ALL_ORDERS,
  Agency_Orders,
  ERROR_ORDERS,
  ORDERS_BY_ID,
  ITEM_STATUS,
  ORDER_STATUS,
  ORDER_CANCEL,
} from "../constents/constents";

const initState = {
  errors: [],
};

const orderReducer = (state = initState, action) => {
  switch (action.type) {
    case ALL_ORDERS:
      return {
        ...state,
        orderList: action.payload,
        errors: "",
        orderStaus: "",
        itemStatus: "",
      };

    case Agency_Orders:
      return {
        ...state,
        agencyorderList: action.payload,
        errors: "",
        orderStaus: "",
        itemStatus: "",
      };

    case "isorderdelivered":
      return {
        ...state,
        isdelivered: action.payload,
        errors: "",
        orderStaus: "",
        itemStatus: "",
      };

    case ORDERS_BY_ID:
      return {
        ...state,
        getOrderDetails: action.payload,
        itemStatus: "",
        orderStaus: "",
      };

    case ITEM_STATUS:
      return { ...state, itemStatus: action.payload, orderStaus: "" };

    case ORDER_STATUS:
      return { ...state, orderStaus: action.payload, itemStatus: "" };

    case ORDER_CANCEL:
      return { ...state, orderCancel: action.payload, itemStatus: "" };

    case ERROR_ORDERS:
      return { ...state, errors: action.payload };

    default:
      return state;
  }
};

export default orderReducer;
