import React, { useEffect, useState } from 'react';
import { MDBDataTableV5 } from 'mdbreact';
import { useDispatch, useSelector } from 'react-redux';
import { adminApproval, userlist } from '../../actions/userAction';
import { nursesByBranchesId,nursesByAgencyId } from '../../actions/agencyAction';
import toast, { Toaster } from 'react-hot-toast';
import { useParams, useHistory, Link } from 'react-router-dom';

const NursesbyAgency = () => {

    const { users, newUser, adminApprovalStatus, nurseListsByAgencyId } = useSelector((state) => state.userReducer);
    const { AGENCY_Data } = useSelector((state) => state.agencyReducer);

    console.log('AGENCY_Data', AGENCY_Data)

    //console.log('login user data',user._id);		
    const [tablebody, setTableBody] = useState([]);
    let count = 0;

    const { user } = useSelector((state) => state.userReducer);

    const Id=user?._id
    // for Get nurses By Agency
    useEffect(() => {
        dispatch(nursesByAgencyId(Id));
    }, [Id])

    

    const dispatch = useDispatch();
    // useEffect(() => {
    //     if (newUser) {
    //         toast.success(newUser.message);
    //     }
    //     dispatch(userlist());
    //     dispatch(nursesByBranchesId(branchId));


    // }, [newUser]);

    const changeStatus = (user_id) => {
        dispatch(adminApproval(user_id));
        // dispatch(userlist());
        // dispatch(nursesByBranchesId(branchId));
    }

    // console.log("users:", users);


    // useEffect(() => {
    //     if (adminApprovalStatus) {
    //         toast.success(adminApprovalStatus.msg);
    //         dispatch(nursesByBranchesId(branchId));
    //     }
    // }, [adminApprovalStatus])
    // Data Table work
    useEffect(() => {
        // const userDataFiltered = users?.map((x) => {
        const userDataFiltered = AGENCY_Data?.map((x) => {

            let jsx = (<>
                <button onClick={(e) => changeStatus(x._id)} className="btn btn-primary" disabled={x.admin_approved == 1 ? true : false} style={{ marginLeft: 10 }}>{x.admin_approved == 1 ? 'Approved' : 'Not Approved'}</button>
                <Link to={`/branch-list-by-agency/nurse-list-by-branch/patient-list-by-nurse/${x._id}`} style={{ marginLeft: "30px" }} className="btn btn-sm btn-primary mr-1" title="">See Patient List</Link>
				<Link to={`/branch-list-by-agency/nurse-list-by-branch/order-list-of-nurse/${x._id}`} style={{ marginLeft: "30px" }} className="btn btn-sm btn-primary mr-1" title="">Order List</Link>
            </>
            )
            let activeStatus = (<>
                {x.admin_approved == 0 ? <span className="badge badge-danger">Not Approved</span> : <span className="badge badge-primary">Approved</span>}
            </>)

            for (const [key, value] of Object.entries(x)) {
                if (value === null) {
                    x[key] = undefined
                }
            }
            x.action = jsx;
            x.admin_approved = activeStatus
            return x

        })


        setTableBody({

            columns: [

                {
                    label: 'User Name',
                    field: 'user_fname',
                    width: 150,
                },
                {
                    label: 'User Email',
                    field: 'user_email',
                    width: 150,
                },
                {
                    label: 'User Status',
                    field: 'admin_approved',
                    width: 150,
                },
                {
                    label: 'Action',
                    field: 'action',
                    width: 10
                }

            ],

            rows: userDataFiltered ? userDataFiltered : [],
        })
        // }, [users])
    }, [nurseListsByAgencyId])



    return <>
        <div className="row clearfix">
            <div className="col-lg-12">
                <div className="card">
                    <div className="header" style={{ marginTop: 20 }}>
                        <h4>Nurse List </h4>
                        <Toaster
                            position="top-center"
                            reverseOrder={false}
                            toastOptions={{
                                style: {
                                    border: '1px solid #713200',
                                    padding: '16px',
                                    color: '#713200',
                                    fontSize: '17px'
                                },
                            }}
                        />
                    </div>
                    <div className="body">
                        <div className="table-responsive">
                            {/* {users?.length && ( */}
                            {nurseListsByAgencyId?.length && (

                                <MDBDataTableV5
                                    noBottomColumns
                                    className="table table-hover js-basic-example dataTable table-custom spacing5"
                                    entriesOptions={[5, 10, 20, 25]}
                                    entries={10}
                                    paging
                                    pagesAmount={15}
                                    data={tablebody}

                                    // searchTop
                                    sortRows={['user_email']}
                                //searchBottom={true}
                                // onPageChange={()=>{ activePage: 2, pagesAmount: 5 }}
                                />)}

                        </div>
                    </div>
                </div>
            </div>

        </div>


    </>



}
export default NursesbyAgency
