import React, { useEffect, useState } from 'react';
import { MDBDataTableV5 } from 'mdbreact';
import { useDispatch, useSelector } from 'react-redux';
import { useParams, useHistory, Link } from 'react-router-dom';
import toast, { Toaster } from 'react-hot-toast';
import { allbranches, branchDeleteID, branchesByAgencyID, patientByNursesid, patientList } from '../../actions/agencyAction';


const PatientList = (props) => {

    const dispatch = useDispatch();
    const { branchesList, branchDelete, branchListsByAgencyId, patientsByNursesId, patientLists } = useSelector((state) => state.agencyReducer);
    const [tablebody, setTableBody] = useState([]);
    let count = 0;

    const { nurseId } = useParams();
    // console.log('agencyId', agencyId);

    // for Get patients By nurse ID
    useEffect(() => {
        dispatch(patientList());
    }, [])

    // console.log("patientLists:", patientLists);

    // console.log("patientsByNursesId:", patientsByNursesId);

    // console.log("branchListsByAgencyId:", branchListsByAgencyId);



    useEffect(() => {
        dispatch(allbranches());
    }, [])

    const DeleteBranch = (branch_id) => {
        dispatch(branchDeleteID(branch_id))
    }

    useEffect(() => {
        if (branchDelete) {
            toast.error(branchDelete.msg);
        }
        dispatch(allbranches());
        dispatch(branchesByAgencyID(nurseId));
    }, [branchDelete])


    useEffect(() => {
        // const userDataFiltered = branchesList?.map((x) => {
        // const userDataFiltered = branchListsByAgencyId?.map((x) => {
        const userDataFiltered = patientLists?.map((x) => {


            // let jsx = (<>
            //     <Link to={`branch-add/${x._id}`}><button type="button" className="btn btn-sm btn-default" title="Order Details" data-for="send" data-tip="true" currentitem="false"><i className="icon-pencil"></i></button></Link>
            //     <button type="button" onClick={(e) => DeleteBranch(x._id)} className="btn btn-sm btn-default" title="Delete" data-for="send" data-tip="true" currentitem="false"><i className="icon-trash"></i></button>
            // </>
            // )

            // x.action = jsx;
            return x
        })

        setTableBody({

            columns: [

                // {
                // 	label: 'Agency Name',
                // 	field: 'order_number',
                // 	width: 150,					
                // },
                {
                    label: 'Patient First Name',
                    field: 'patient_fname',
                    width: 150,
                },
                {
                    label: 'Patient Last Name',
                    field: 'patient_lname',
                    width: 150,
                },
                {
                    label: 'Patient Phone Number',
                    field: 'patient_phone',
                    width: 150,
                },
                {
                    label: 'Patient DOB',
                    field: 'patient_dob',
                    width: 150,
                },


                // {
                //     label: 'Action',
                //     field: 'action',
                //     width: 10
                // }

            ],

            rows: userDataFiltered ? userDataFiltered : []

        })
    }, [patientLists])

    return <>
        {/* test */}
        <div className="row clearfix">
            <div className="col-lg-12">
                <div className="card">
                    <div className="header" style={{ marginTop: 20 }}>
                        <h4>Patient List </h4>
                        {/* <Link to="/branch-add" className="btn btn-sm btn-primary mr-1" title="">Add New Agency</Link> */}

                        <Toaster
                            position="top-center"
                            reverseOrder={false}
                            toastOptions={{
                                style: {
                                    border: '1px solid #713200',
                                    padding: '16px',
                                    color: '#713200',
                                    fontSize: '17px'
                                },
                            }}
                        />
                    </div>
                    <div className="body">
                        <div className="table-responsive">
                            {patientLists?.length && (
                                <MDBDataTableV5
                                    noBottomColumns
                                    className="table table-hover js-basic-example dataTable table-custom spacing5"
                                    entriesOptions={[5, 10, 20, 25]}
                                    entries={5}
                                    paging
                                    pagesAmount={15}
                                    data={tablebody}

                                    // searchTop
                                    sortRows={['patient_fname']}
                                //searchBottom={true}
                                // onPageChange={()=>{ activePage: 2, pagesAmount: 5 }}
                                />)}

                        </div>
                    </div>
                </div>
            </div>

        </div>


    </>



}

export default PatientList
