import{MESSENGER_ERROR,LOGIN_USER,GET_USERS, ONLINE_USERS} from '../constents/constents';
import {io} from 'socket.io-client';
//const socket = io("http://localhost:8000");
const socket = io("/");
window.socket = socket;
const initState = {    
    errors:[],
    friendData:'',
    usersOnline:[],
    socket: socket
};


const messangerReducer = (state = initState, action) => {
switch(action.type){
        case LOGIN_USER:              
                return{...state, conversation: action.payload}
        case GET_USERS:                     
                return{...state, friendData: action.payload}
        case MESSENGER_ERROR:                    
                return {...state, errors: action.payload};
        case ONLINE_USERS:                    
                return {...state, usersOnline: action.payload};
        default:
        return state;
   
    }

}

export default messangerReducer;