import React, { useEffect, useState, useRef } from "react";
import { format } from "timeago.js";
import axios from "axios";
import "./chatupdated.css";
import { io } from 'socket.io-client';
import { useSelector } from "react-redux";
import ChatBox from "./chatbox";

var newMessageArr=[]  
const Chatupdated = () => {
  const [allagencies, setallagencies] = useState([]);
  const [messages, setMessages] = useState([]);
  const [allnurses, setallnurses] = useState([]);
  const [agencyid, setagencyid] = useState("");
  const [branchid, setbranchid] = useState("");
  const [allbranches, setallbranches] = useState([]);
  const [allpatients, setallpatients] = useState([]);
  const [nurseId, setnurseId] = useState("");
  const [toggle, settoggle] = useState(false);
  const [dis, setdis] = useState(false);
  const [currentChat, setCurrentChat] = useState(null);
  const [newMessage, setNewMessage] = useState("");
  const [convoId, setconvoId] = useState("");
  const [search, setsearch] = useState("");
  const [search1, setsearch1] = useState("");
  const [name, setname] = useState("");
  const socket = io("https://server.appsstaging.com:3009");
  const user = useSelector((state) => state.userReducer.user);
  const scrollRef = useRef(null);
  const alldetails = () => {
    axios.get(`https://server.appsstaging.com:3009/api/getdetails`).then((res) => {
      setallagencies(res.data.agencynames);
      setallnurses(res.data.nurses);
    });
  };

  useEffect(() => {
    alldetails();
  }, []);


  const selectedAgency = (e) => {
    setagencyid(e.target.value);

    if (e.target.value) {
      axios
        .get(`https://server.appsstaging.com:3009/api/getbranches`, {
          params: {
            id: e.target.value,
          },
        })
        .then((res) => {
          setallbranches(res.data.branchesnames);
        });
    }
  };

  const searchFilter = (e) => {
    e.preventDefault();
    setdis(true);

    if (agencyid == "" && branchid === "") {
      alldetails();
    }

    if (agencyid !== "" && branchid === "") {
      axios
        .get(`https://server.appsstaging.com:3009/api/getfilter1`, {
          params: {
            agencyid: agencyid,
          },
        })
        .then((res) => {
          setallnurses(res.data.nurses);
        });
    }

    if (agencyid === "" && branchid !== "") {
      axios
        .get(`https://server.appsstaging.com:3009/api/getfilter2`, {
          params: {
            branchid: branchid,
          },
        })
        .then((res) => {
          setallnurses(res.data.nurses);
        });
      setbranchid("");
    }

    if (agencyid !== "" && branchid !== "") {
      axios
        .get(`https://server.appsstaging.com:3009/api/getfilter3`, {
          params: {
            agencyid: agencyid,
            branchid: branchid,
          },
        })
        .then((res) => {
          setallnurses(res.data.nurses);
        });
      setbranchid("");
    }
  };

  const setId = (id) => { 
    settoggle(!toggle);
    setnurseId(id);
    axios
      .get(`https://server.appsstaging.com:3009/api/getnursepatients`, {
        params: {
          id: id,
        },
      })
      .then((res) => {
        setallpatients(res.data.patients);
      });
  };

  const getConvoData = (id, fname, lname) => {
    if(newMessageArr.includes(id)){ 
      newMessageArr.pop(id)  
    }
    if (id != convoId) {
      setMessages([])
      socket.emit("get_messages", {
        "sender_id": user?._id,
        "receiver_id": id
      });
    }  
    setNewMessage("");
    setname(`${fname} ${lname}`);
    setconvoId(id);
  };

  const submitHandle = async (e) => {
    e.preventDefault();
    if (newMessage) {
      socket.emit("send_message", {
        "sender_id": user?._id,
        "receiver_id": convoId,
        "message": newMessage,
      });
      socket.emit("get_message", {
        "sender_id": user?._id,
        "receiver_id": convoId
      });
      setCurrentChat(null)
      setNewMessage("");
    }
  };
  const enterKye = (e) => {
    if (e.key === "Enter") {
      e.preventDefault();
      if (newMessage) {
        socket.emit("send_message", {
          "sender_id": user?._id,
          "receiver_id": convoId,
          "message": newMessage,
        });
        socket.emit("get_message", {
          "sender_id": user?._id,
          "receiver_id": convoId
        });
        setNewMessage("".trim());
        setCurrentChat(null)
      }
    }
  }
  useEffect(() => {
    scrollRef?.current &&
      scrollRef.current.scrollIntoView();
  }, [messages]);


  const check = allnurses.filter((e) => {
    if (search === "") {
      return e;
    } else {
      return `${e?.user_fname}`?.toLowerCase().includes(search);
    }
  });

  const check1 = allpatients.filter((e) => {
    if (search1 === "") {
      return e;
    } else {
      return `${e?.patient_fname}`?.toLowerCase().includes(search1);
    }
  });
  useEffect(() => {
    socket.on("response", (data) => { 
      if (data?.object_type == "get_message") {
        if(!newMessageArr.includes(data?.data[0]?.sender_id)){ 
          newMessageArr.push(data?.data[0]?.sender_id)  
        }
        setCurrentChat(data?.data[0])
      }
      else {
        setMessages(data?.data)
      }
    });
  }, [socket])
  useEffect(() => {
    socket.emit("get_messages", {
      "sender_id": user?._id,
      "receiver_id": convoId
    });
  }, [])

  useEffect(() => {
    if (currentChat) {
      if ((currentChat?.sender_id == user?._id && currentChat?.receiver_id == convoId) || (currentChat?.sender_id == convoId && currentChat?.receiver_id == user?._id)) {
        if (messages?.some(obj => obj?._id === currentChat?._id)) {
          setMessages(messages);
          setCurrentChat(null)
        } else {
          setMessages((messages) => [...messages, currentChat]);
          setCurrentChat(null)
        }
      }
    }
  }, [currentChat])
    return (
    <div>
      <div className="container">
        <section className="chat-sec">
          <form action="true" className="filterForm">
            <select onChange={(e) => selectedAgency(e)}>
              <option value="" selected>
                Search Agency
              </option>
              {allagencies.map((a, i) => (
                <option key={i} value={a._id}>{a.hospital_name}</option>
              ))}
            </select>

            <select onChange={(e) => setbranchid(e.target.value)}>
              <option
                value
                selected
                style={{ disabled: dis ? "false" : "true" }}
              >
                Search Branch
              </option>
              {agencyid
                ? allbranches.map((b, i) => (
                  <option key={i} value={b._id}>{b.branch_name}</option>
                ))
                : null}
            </select>
            <button className="filterBtn" onClick={(e) => searchFilter(e)}>
              <img src="../assets/images/search-interface.png" alt="img" />
            </button>
          </form>

          <div className="row chat-row">
            <div className="col-lg-4 col-md-4 col-12 p-0">
              <div className="left-chat-box">
                <form className="chat-search-bar">
                  <div className="form-group">
                    <input
                      type="text"
                      onChange={(e) => setsearch(e.target.value.toLowerCase())}
                      // disabled
                      placeholder="Search Nurse..."
                    />
                    <a href="#!" className="search-icon">
                      <img
                        src="../assets/images/search-interface.png"
                        alt="img"
                      />
                    </a>
                    {/* <br /> */}
                  </div>
                </form>
                <div
                  className="accordion chat-listing"
                  id="accordionExample"
                  style={{ borderWidth: 0 }}
                >
                  <div className="accordion-item" style={{ borderWidth: 0 }}>

                    {check?.map((an, i) => (

                      <div
                        key={i}
                        className="accordion-header"
                        id="headingOne"
                        style={{ borderWidth: 0 }}
                      >
                        <button
                          className="accordion-button itemWrap"
                          type="button"
                          data-bs-toggle="collapse"
                          data-bs-target="#collapseOne"
                          aria-expanded="true"
                          aria-controls="collapseOne"
                          onClick={() => setId(an?._id)}
                        >
                          <a href="#!" className="chat-item">
                            <div className="rounded-circle img-profile">
                              <img
                                style={{
                                  width: "70px",
                                  height: "70px",
                                  borderRadius: "50%",
                                  marginLeft: "-19px",
                                }}
                                src={
                                  an?.user_image
                                    ? an?.user_image
                                    : "../assets/images/nurse.png"
                                }
                              />
                            </div>
                            <span className="text-box">
                              <h1 className="title pb-2">
                                {an?.user_fname} {an?.user_lname}
                              </h1>
                              <p className="desc">
                                Agency Name:{" "}
                                <span>{an?.agency_id?.hospital_name}</span>
                              </p>
                              <p className="desc">
                                Branch Name:{" "}
                                <span>{an?.branch_id?.branch_name}</span>
                              </p>
                            </span>
                          </a>
                        </button>

                        {allpatients.map((ap, i) => (

                          <div key={i}>
                            {toggle && an?._id === ap?.user_id ? (
                              <div
                                id="collapseOne"
                                className={`"accordion-collapse collapse" ${toggle && "show"
                                  }`}
                                aria-labelledby="headingOne"
                                data-bs-parent="#accordionExample"
                              >
                                <div className="accordion-body">
                                  {i === 0 ? <p className="pateintHeading pb-1">
                                    Patients
                                  </p> : null}

                                  <div style={{ marginLeft: "27px" }}>
                                    <ul
                                      style={{ listStyle: "none" }}
                                      className="nav nav-pills m-0 ps-4 flex-column"
                                      id="pills-tab"
                                      role="tablist"
                                    >
                                      <li
                                        className="nav-item"
                                        role="presentation"
                                      >
                                        <button
                                          style={{ borderWidth: 0,backgroundColor:newMessageArr.includes(ap?._id)==true?"#8aedfc":"" }}
                                          onClick={() =>
                                            getConvoData(
                                              ap?._id,
                                              ap?.patient_fname,
                                              ap?.patient_lname
                                            )
                                          }
                                          className="nav-link active"
                                          id="pills-patients-1-tab"
                                          data-bs-toggle="pill"
                                          data-bs-target="#pills-patients-1"
                                          type="button"
                                          role="tab"
                                          aria-controls="pills-patients-1"
                                          aria-selected="true"
                                        >
                                          {i + 1}.
                                          {"       "}{ap?.patient_fname}{" "}
                                          {ap?.patient_lname}
                                        </button>


                                        {ap.messagesCount > 0 ? (
                                          <span
                                            style={{ cursor: "pointer" }}
                                            key={ap._id}
                                          >
                                            <span
                                              className="notification-dot bg-azura"
                                              style={{
                                                padding: 2,
                                                color: "white",
                                              }}
                                            >
                                              {ap.messagesCount}
                                            </span>
                                          </span>
                                        ) : (
                                          ""
                                        )}
                                      </li>
                                    </ul>
                                  </div>

                                </div>
                              </div>
                            ) : null}
                          </div>
                        )
                        )}
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            </div>

            <div className="col-lg-8 col-md-8 col-12 p-0">
              <div className="tab-content" id="pills-tabContent">
                <div
                  className="tab-pane fade show active"
                  id="pills-patients-1"
                  role="tabpanel"
                  aria-labelledby="pills-patients-1-tab"
                >
                  <div className="chatting-box">
                    <a href="#!" className="header">
                      <div className="img-box">
                        {name ? (
                          <img src="../assets/images/848685-200.png" alt="img" />
                        ) : null}
                      </div>
                      <div className="text-box">
                        <h1 className="title">{name.toUpperCase()}</h1>
                      </div>
                    </a>
                    <ChatBox messages={messages} scrollRef={scrollRef} user={user} name={name} submitHandle={submitHandle} enterKye={enterKye} setNewMessage={setNewMessage} newMessage={newMessage} />

                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </div>
  );
};

export default Chatupdated;
