// Here to get branches by agency id
export const branchesByAgencyID = (agencyId) => {
  // console.log('agencyId_in_action', agencyId);
  return async (dispatch) => {
    try {
      const {
        data: { branchListsByAgencyId },
      } = await axios.get(`/api/branchListByAgencyId/${agencyId}`);
      // const { data: { branchListByAgencyId } } = await axios.get(process.env.REACT_APP_API_URL + `/api/branchListByAgencyId/${order_id}`);

      dispatch({
        type: Branch_List_BY_agency_ID,
        payload: branchListsByAgencyId,
      });
    } catch (error) {
      dispatch({ type: ERROR_AGENCY, payload: error.response.data.error });
    }
  };
};

