// here to get All AGENCY 2nd
export const allAgency = () => async (dispatch) => {
  try {
    const userData = cookies.get("userData");
    const user_id = userData._id;
    // console.log("user_id_in_agency_action:", user_id);

    // const { data: { agencyList } } = await axios.get('/api/agencyList');
    const {
      data: { agencyList },
    } = await axios.get(`/api/agencyList/${user_id}`);
    // const { data } = await axios.get(`/api/agencyList/${user_id}`);
    // console.log("agencyListInAction:",agencyList);
    dispatch({ type: ALL_AGENCY, payload: agencyList });
    // dispatch({ type: ALL_AGENCY, payload: data });
  } catch (error) {
    dispatch({ type: ERROR_AGENCY, payload: error.response?.data?.error });
  }
};

export const AgencyAdd = (state) => {
  return async (dispatch) => {
    const config = {
      headers: {
        "Content-Type": "application/json",
      },
    };
    try {
      const { data } = await axios.post("/api/AddAgency", state, config);
      // const { data } = await axios.post(process.env.REACT_APP_API_URL + '/api/AddAgency', state, config);

      dispatch({ type: ADD_AGENCY, payload: data });
    } catch (error) {
      dispatch({ type: ERROR_AGENCY, payload: error.response.data.error });
    }
  };
};

