import React, {useEffect, useState} from 'react';
import{useParams, useHistory} from 'react-router-dom';
import Dropzone from '../common/DropzoneExample';
import {useDispatch,useSelector} from 'react-redux';
import DatePicker from "react-datepicker";
import toast, { Toaster } from 'react-hot-toast';
import "react-datepicker/dist/react-datepicker.css";
import * as moment from 'moment';
import {AgencyAdd,agencyEditID} from '../../actions/agencyAction';
import {allCategory, productEditID, productSave } from '../../actions/productAction';



const AddProduct = (props) => {	

    const dispatch  = useDispatch();
    const{getAllCategory, saveProduct,errors,productEdit} = useSelector((state) => state.productReducer);		   	
   

    const {edit} = useParams();
      console.log(edit);
   
    const[state, setState] = useState({        
		id:'',
		product_name:'',
        product_detail:'',
        product_weight:'',
        product_height:'',
        product_need:'',
        product_pricing_type_a:'',
        product_pricing_type_b:'',
        product_pricing_type_c:''        
    });
    const [featuresArr, setFeaturesArr] = useState([]);
    
    const handleInputs = (e) => {        
        setState({
            ...state,
            [e.target.name] : e.target.value
        })        
    }

    const handleInputsImage = (e) => {    
 
        setState({
            ...state,
            [e.target.name] : e.target.files,            
        })        
    }

            useEffect(() => {
                dispatch(allCategory());
            }, [])

         const saveProductData = async (e) => {
            e.preventDefault();  

            state.id = edit ? edit: '';           
            dispatch(productSave({...state, featuresArr}))
         }

         useEffect(() => {             
             if(edit){                
                dispatch(productEditID(edit));	
             }
         },[edit])

         // for Saved
         useEffect(() => {
            if(saveProduct){
                
                setState({
                    id:'',
                    product_name:'',
                    product_detail:'',
                    product_weight:'',
                    product_height:'',
                    product_need:'',
                    product_pricing_type_a:'',
                    product_pricing_type_b:'',
                    product_pricing_type_c:'' 
                })
                window.location.reload(false);
                toast.success(saveProduct.msg);
            }
            
         },[saveProduct])
         // for errors
         useEffect(() => {                       
            if(errors && errors.length > 0){
                errors.map(error => (
                toast.error(error.msg)
                 ))
            }
         },[errors])

         useEffect(() => {
             if(edit){
                if(productEdit){
                    setState({
                        ...state,
                        product_name: productEdit.product_name ? productEdit.product_name : '',
                        product_detail: productEdit.product_detail ? productEdit.product_detail : '',
                        category_id: productEdit.category_id ? productEdit.category_id : '',
                        product_weight: productEdit.product_weight ? productEdit.product_weight : '',
                        product_height: productEdit.product_height ? productEdit.product_height : '',
                        product_need: productEdit.product_need ? productEdit.product_need : '',
                        product_pricing_type_a: productEdit.product_pricing_type_a ? productEdit.product_pricing_type_a : '',
                        product_pricing_type_b: productEdit.product_pricing_type_b ? productEdit.product_pricing_type_b : '',
                        product_pricing_type_c: productEdit.product_pricing_type_c ? productEdit.product_pricing_type_c : '',
                                            
                    })
                }
            }
         },[productEdit])

        const addFeaturesClick = () => {
            setFeaturesArr([
              ...featuresArr,
              {
                feature_title: "",
                feature_detail: "",
                feature_price: "",
              },
            ]);
          };

          const removeFeaturesClick = (i) => {
            let statesClone = [...featuresArr];
            statesClone.splice(i, 1);
            setFeaturesArr(statesClone);
          };

          const handleChange = (event, index) => {
            const { name, value } = event.target;
            // console.log(name, value);
            let variationStatesClone = [...featuresArr];
            variationStatesClone[index] = {
              ...variationStatesClone[index],
              [name]: value,
            };
            setFeaturesArr(variationStatesClone);
          };

          const createUIFeatureFields = () => {
            return featuresArr?.map((el, index) => {
              return (
                <div className="row clearfix" style={{marginTop:20}}>
                                    <div className="col-lg-4 col-md-12">
                                        <div className="form-group">                                            
                                            <label style={{color:'#17C2D7'}}>Title</label>
                                            <input type="text" name="feature_title" value={el.feature_title}  onChange={(event) => {
              handleChange(event, index);
            }}  className="form-control" placeholder="Title" />                                            
                                        </div>
                                    </div>

                                    <div className="col-lg-4 col-md-12">
                                        <div className="form-group">                                        
                                            <label style={{color:'#17C2D7'}}>Feature Details</label>
                                            <input type="text" name="feature_detail" value={el.feature_detail}  onChange={(event) => {
              handleChange(event, index);
            }}  className="form-control" placeholder="Feature Details" />                                            
                                        </div>
                                    </div>
                                    
                                    <div className="col-lg-3 col-md-12">
                                        <div className="form-group">                                        
                                            <label style={{color:'#17C2D7'}}>Feature Price</label>
                                            <input type="text" name="feature_price" value={el.feature_price}  onChange={(event) => {
              handleChange(event, index);
            }}  className="form-control" placeholder="Feature Price" />                                            
                                        </div>                                        
                                    </div>
                                    <div className="col-lg-1 col-md-12">
                                    <i className="fa fa-trash-o" onClick={() => {removeFeaturesClick(index);}} style={{cursor:'pointer', marginTop:40}}></i> 									                                      
                                    </div>                                

                                </div>
              );
            });
          };


	return <>         
<div className="container-fluid">
                <div className="block-header">
                    <div className="row clearfix">
                        <div className="col-md-6 col-sm-12">
                            <h2>Add New Product</h2>                            
                        </div>                       
                    </div>
                </div>
                <div className="row clearfix">
                    
                <Toaster
						position="top-right"
						reverseOrder={false}
						toastOptions={{							
							style: {
							  border: '1px solid #713200',
							  padding: '16px',
							  color: '#713200',
							  fontSize: '17px'
							},
						  }}
						/>

                    <div className="col-xl-8 col-lg-8 col-md-7">

                    <form onSubmit={saveProductData}>
	                  <div className="card">
                      
                            <div className="body">
                                
                                <div className="row clearfix">

                                <div className="col-lg-4 col-md-12">
                                        <div className="form-group">
                                            <input type="hidden" name="id" value={edit ? edit : ''} />
                                            <label style={{color:'#17C2D7'}}>Category Name</label>
                                            <select className="form-control" name="category_id" value={state.category_id} onChange={handleInputs}>
                                                <option value="">-- Select Category --</option>
                                                {getAllCategory ? getAllCategory.map((agn) => (
                                                <option value={agn._id} key={agn._id} selected={state.category_id?"selected":""}>{agn.category_name}</option>
                                                )) : 'No Record found!'
							                }
                                            </select>
                                            <span style={{color: 'red'}}>{errors && errors.category_id ? errors.category_id.msg : ''}</span>
                                        </div>
                                </div>
                                    <div className="col-lg-4 col-md-12">
                                        <div className="form-group">                                            
                                            <label style={{color:'#17C2D7'}}>Product Name</label>
                                            <input type="text" name="product_name" value={state.product_name} onChange={handleInputs}  className="form-control" placeholder="Product Name" />
                                            <span style={{color: 'red'}}>{errors && errors.product_name ? errors.product_name.msg : ''}</span>
                                        </div>
                                    </div>

                                    <div className="col-lg-4 col-md-12">
                                        <div className="form-group">                                        
                                            <label style={{color:'#17C2D7'}}>Product Details</label>
                                            <input type="text" name="product_detail" value={state.product_detail} onChange={handleInputs}  className="form-control" placeholder="Product Details" />                                            
                                        </div>
                                    </div>
                                   
                                   
                                </div>

                                <div className="row clearfix">

                                    <div className="col-lg-4 col-md-12">
                                            <div className="form-group">                                            
                                                <label style={{color:'#17C2D7'}}>Weight</label>
                                                <input type="text" name="product_weight" value={state.product_weight} onChange={handleInputs}  className="form-control" placeholder="Product Weight" />                                                
                                            </div>
                                        </div>

                                        <div className="col-lg-4 col-md-12">
                                            <div className="form-group">                                        
                                                <label style={{color:'#17C2D7'}}>Height</label>
                                                <input type="text" name="product_height" value={state.product_height} onChange={handleInputs}  className="form-control" placeholder="Product Height" />                                            
                                            </div>
                                        </div>

                                        <div className="col-lg-4 col-md-12">
                                            <div className="form-group">                                        
                                                <label style={{color:'#17C2D7'}}>Needs 2</label>
                                                <input type="text" name="product_need" value={state.product_need} onChange={handleInputs}  className="form-control" placeholder="Product Needs" />                                            
                                            </div>
                                        </div>

                                    </div>

                                <div className="row clearfix">

                                    <div className="col-lg-4 col-md-12">
                                            <div className="form-group">                                            
                                                <label style={{color:'#17C2D7'}}>Pricing Type Agency 1</label>
                                                <input type="text" name="product_pricing_type_a" value={state.product_pricing_type_a} onChange={handleInputs}  className="form-control" placeholder="Pricing Type Agency 1" />                                                
                                            </div>
                                        </div>

                                        <div className="col-lg-4 col-md-12">
                                            <div className="form-group">                                        
                                                <label style={{color:'#17C2D7'}}>Pricing Type Agency 2</label>
                                                <input type="text" name="product_pricing_type_b" value={state.product_pricing_type_b} onChange={handleInputs}  className="form-control" placeholder="Pricing Type Agency 2" />                                            
                                            </div>
                                        </div>

                                        <div className="col-lg-4 col-md-12">
                                            <div className="form-group">                                        
                                                <label style={{color:'#17C2D7'}}>Pricing Type Agency 3</label>
                                                <input type="text" name="product_pricing_type_c" value={state.product_pricing_type_c} onChange={handleInputs}  className="form-control" placeholder="Pricing Type Agency 3" />                                            
                                            </div>
                                        </div>

                                    </div>

                                <div className="row clearfix">
                                    {!productEdit ? 
                                     <div className=" col-lg-4 col-md-12" style={{marginLeft:15}}>                                     
                                            <div className="form-group">                                                    
                                                <input type="file" multiple  name="product_images" onChange={handleInputsImage} className="form-control" id="inputGroupFile01" />                                                
                                                <label className="custom-file-label" htmlFor="inputGroupFile01">Upload Product Images</label>
                                            </div>
                                     </div>
                                     : '' 
                                    }

                                    {/* <div className="col-lg-12 col-md-12">
                                        <Dropzone onChange={handleInputsImage} name="product_images[]" />                                 
                                    </div> */}
                                </div>

                                {/* <div className="row clearfix">
                                   <button type="button" onClick={addFeaturesClick}  className="btn btn-round btn-primary mr-1">Add Feature</button>
                                </div> */}
                              
                                {createUIFeatureFields()}

                                    <button type="submit"  className="btn btn-round btn-primary mr-1" style={{marginTop:20}}>Save</button>
                             
                            
                            </div>                            
                        </div>                          
                        </form>           
                        </div>                       
                     </div>
                     </div>
                   
	</>



}
export default AddProduct
