import React, { useEffect, useState, useRef } from "react";
import { format } from "timeago.js";
import "./chatupdated.css";

const ChatBox = ({ messages, scrollRef, setNewMessage, newMessage, submitHandle, enterKye, name, user }) => {
    return (
        <div>
            <div className="chat-middle">
                <ul className="message_data">
                    {name ? <>
                        {messages?.map((item, i) =>
                        (
                        <div
                            ref={scrollRef}
                            key={i}
                            className={
                                item?.sender_id == user?._id
                                    ? "message-sender-container"
                                    : ""
                            }
                        >
                            <div className="rounded-circle img-profile">
                                <img
                                    style={{
                                        width: "60px",
                                        height: "60px",
                                        borderRadius: "50%",
                                        marginLeft: "10px",
                                    }}
                                    src={
                                        item?.sender_id == user?._id
                                            ? "../assets/images/superadmin.png"
                                            : "../assets/images/nurse.png"
                                    }
                                />
                            </div>
                            <li
                                className={
                                    item?.sender_id == user?._id
                                        ? "message sender"
                                        : "message receiver"
                                }
                            >
                                <p className="message-content">{item?.message}</p>
                                <p className="date-time">
                                    {format(item?.createdAt)}
                                </p>
                            </li>
                        </div>
                        ))}
                    </> : <>
                        <h4 style={{ marginTop: "20%", color: "grey", textAlign: "center" }}>
                            Open your chat first
                        </h4>
                    </>}

                </ul>
            </div>
            {name ? (
                <div className="chat-bottom">
                    <div className="form-group">
                        <form onSubmit={submitHandle} onKeyPress={enterKye}>
                            <textarea
                                type="text"
                                row=""
                                className="form-control"
                                placeholder="Enter text here..."
                                onChange={(e) => setNewMessage(e.target.value)}
                                value={newMessage}
                            ></textarea>
                            <button type="submit" value="Send">
                                <img
                                    src="../assets/images/paper-plane.png"
                                    alt="img"
                                />
                            </button>
                        </form>
                    </div>
                </div>
            ) : null}
        </div>
    );
};

export default ChatBox;
