import React, { useEffect, useState } from 'react';
import { useParams, useHistory } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import toast, { Toaster } from 'react-hot-toast';
import "react-datepicker/dist/react-datepicker.css";
import axios from "axios";
import { allAgency, NurseAdd } from '../../actions/agencyAction';

function formatPhoneNumber(value) {
    if (!value) return value;
    const phoneNumber = value.replace(/[^\d]/g, "");
    const phoneNumberLength = phoneNumber.length;
    if (phoneNumberLength < 4) return phoneNumber;
    if (phoneNumberLength < 7) {
      return `(${phoneNumber.slice(0, 3)}) ${phoneNumber.slice(3)}`;
    }
    return `(${phoneNumber.slice(0, 3)}) ${phoneNumber.slice(
      3,
      6
    )}-${phoneNumber.slice(6, 10)}`;
  }
const AddNurse = (props) => {

    const dispatch = useDispatch();
    const { nurseSaved, errors} = useSelector((state) => state.agencyReducer);
    const { user } = useSelector((state) => state.userReducer);
    const [branches, setbranches] = useState([])

    console.log("user", user)

    useEffect(() => {
        axios.get(`https://server.appsstaging.com:3018/api/getagencybranches`, {
            params: {
                agencyId: user?._id
            },
        }).then((res) => {
            console.log("res.data", res.data.data)
            setbranches(res.data.data);
        });
    }, [])

    console.log("branches", branches)
    console.log("nurseSaved", nurseSaved)
    
    const { edit } = useParams();

    const [state, setState] = useState({
        user_fname: '',
        user_lname: '',
        user_email: '',
        user_password: '',
        user_retypepassword:'',
        user_phone:'',
        agency_id: user?._id,
        branch_id: '',
        user_type: 'nurse',
        admin_approved: 1,
        user_is_verified: 1,
        user_is_profile_complete: 1


    });
     const handleInputs = (e) => {
              setState({
                ...state,
                [e.target.name]: e.target.value
            }) 
    }
    const handlePhone = (e) => {
        const formattedPhoneNumber = formatPhoneNumber(e.target.value); 
        setState({
          ...state,
          [e.target.name]: formattedPhoneNumber
      }) 
}
     const saveNurse = async (e) => {
        e.preventDefault();
        state.id = edit ? edit : '';
        dispatch(NurseAdd(state))
    }


    // for Saved
    useEffect(() => {
        if (nurseSaved) {
            toast.success(nurseSaved.message);
            setState({
                user_fname: '',
                user_lname: '',
                user_email: '',
                user_password: '',
                user_retypepassword:'',
                user_phone:'',

            })

            dispatch(allAgency());
        }

    }, [nurseSaved])
    // for errors
    useEffect(() => {
        if (errors && errors.length > 0) {
            errors.map(error => (
                toast.error(error.msg)
            ))
        }
    }, [errors])

   

    console.log(state)

    return <>
        <div className="container-fluid">
            <div className="block-header">
                <div className="row clearfix">
                    <div className="col-md-6 col-sm-12">
                        <h2>Add New Nurse</h2>
                    </div>
                </div>
            </div>
            <div className="row clearfix">

                <Toaster
                    position="top-right"
                    reverseOrder={false}
                    toastOptions={{
                        style: {
                            border: '1px solid #713200',
                            padding: '16px',
                            color: '#713200',
                            fontSize: '17px'
                        },
                    }}
                />

                <div className="col-xl-8 col-lg-8 col-md-7">

                    <form onSubmit={saveNurse}>
                        <div className="card">

                            <div className="body">

                                {/* <div className="row clearfix"> */}
                                    <div className="col-lg-4 col-md-12">
                                        <div className="form-group">

                                            <label style={{ color: '#17C2D7' }} >First Name</label>
                                            <input className="form-control" onChange={handleInputs} type="text" name="user_fname" placeholder='First Name' />

                                        </div>
                                    </div>

                                    
                                    <div className="col-lg-4 col-md-12">
                                        <div className="form-group">

                                            <label  style={{ color: '#17C2D7' }} >Last Name</label>
                                            <input className="form-control" onChange={handleInputs} type="text" name="user_lname" placeholder='Last Name' />

                                        </div>
                                    </div>
                                   

                                    <div className="col-lg-4 col-md-12">
                                        <div className="form-group">

                                            <label  style={{ color: '#17C2D7' }} >Email</label>
                                            <input className="form-control" onChange={handleInputs} type="email" name="user_email" placeholder='Email' />

                                        </div>
                                    </div>

                                    <div className="col-lg-4 col-md-12">
                                        <div className="form-group">

                                            <label style={{ color: '#17C2D7' }} >Password</label>
                                            <input className="form-control" onChange={handleInputs} type="password" name="user_password" placeholder='Password' />

                                        </div>
                                    </div>
                                    <div className="col-lg-4 col-md-12">
                                        <div className="form-group">

                                            <label style={{ color: '#17C2D7' }} >Re-Type Password</label>
                                            <input className="form-control" onChange={handleInputs} type="password" name="user_retypepassword" placeholder='Retype Password' />

                                        </div>
                                    </div>
                                    <div className="col-lg-4 col-md-12">
                                        <div className="form-group">

                                            <label style={{ color: '#17C2D7' }} >Contact Number</label>
                                            <input className="form-control" value={state.user_phone} onChange={(e)=>handlePhone(e)}  name="user_phone" placeholder='Contact Number' />

                                        </div>
                                    </div>


                                    <div className="col-lg-4 col-md-12">
                                        <div className="form-group">


                                            <label  style={{ color: '#17C2D7' }} >Agency Name</label>
                                            <input className="form-control" name="AgencyName" value={user?.hospital_name} style={{ disabled: "true" }} />

                                        </div>
                                    </div>


                                    <div className="col-lg-4 col-md-12">
                                        <div className="form-group">

                                            <label  style={{ color: '#17C2D7' }} >Branch Name</label>
                                            <select className="form-control" name="branch_id" onChange={handleInputs}>
                                                <option value="">-- Select Branch --</option>
                                                {branches.map((agn) => (
                                                    <option value={agn._id} key={agn._id}>{agn.branch_name}</option>
                                                ))
                                                }
                                            </select>
                                            <span style={{ color: 'red' }}>{errors && errors.hospital_id ? errors.hospital_id.msg : ''}</span>

                                        </div>
                                    </div>


                                    {/* <div className="col-lg-4 col-md-12">
                                        <div className="form-group">
                                        <label style={{color:'#17C2D7'}}>Branch Name</label>
                                            <input type="text" name="branch_name" value={state.branch_name} onChange={handleInputs}  className="form-control"  placeholder="Branch Name" />
                                            <span style={{color: 'red'}}>{errors && errors.branch_name ? errors.branch_name.msg : ''}</span>
                                        </div>
                                    </div> */}




                                {/* </div> */}

                                <div className="col-lg-4 col-md-12">
                                <button type="submit" className="btn btn-round btn-primary mr-1">Save</button>
                            </div>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </>

}
export default AddNurse
